<template>
  <el-upload
    class="upload-demo"
    action="https://jsonplaceholder.typicode.com/posts/"
    :on-preview="handlePreview"
    :on-remove="handleRemove"
    :on-error="handerror"
    :file-list="fileList"
    list-type="picture"
  >
    <el-button size="small" type="primary">上传素材</el-button>
    <el-alert
      title="文件大小过大"
      type="error"
      description="请上传低于500kb大小的文件"
      show-icon
      v-show="error"
    >
    </el-alert>
  </el-upload>
</template>

<script>
import { sucai } from "../api/sucai";
export default {
  watch: {
    file() {},
  },
  data() {
    return {
      error:false,
      timer:null,
      fileList: [
        // {
        //   name: "惊雷素材",
        //   url: require("@/assets/sucais.png"),
        // },
        // {
        //   name: "红素材",
        //   url: require("@/assets/sucais.png"),
        // },
      ],
    };
  },
  methods: {
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handerror() { 
      this.timer = setInterval(() => {
        this.error = false
      }, 3000);
      this.error = true
    },
  },
  created() {
    sucai({pageNum:1,pageSize:'10'}).then((res) => {
      console.log(res);
      let arr = res.rows;
      arr.forEach((item) => {
        this.$set(item, "url", require("@/assets/sucais.png"));
      });
      this.fileList = arr;
      sessionStorage.setItem("file", JSON.stringify(this.fileList));
    });
  },
  mounted() {

  },
};
</script>

<style scoped>
.el-button {
  width: 107px;
  height: 34px;
  background: linear-gradient(180deg, #eec598 0%, #c58651 100%);
  border-radius: 6px 6px 6px 6px;
  border: none;
  position: absolute;
  left: 16px;
}
::v-deep .el-upload-list {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  gap: 58px;
  top: 72px;
  padding-left: 22px;
  box-sizing: border-box;
}
::v-deep .el-upload-list__item.is-success {
  width: 125px;
  height: 180px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0;
  background: #171c1f;
  border: none;
}
::v-deep .el-upload-list__item.is-success img {
  width: 125px;
  height: 132px !important;
  margin: 0;
  background: #262a2d;
}
::v-deep .el-icon-close {
  z-index: 999;
}
::v-deep .el-upload-list__item-name {
  font-size: 20px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: lighter;
  color: #f4f0e9;
  padding-left: 5px;
}
::v-deep .el-upload-list--picture .el-upload-list__item-status-label {
  width: 40px;
  height: 31px;
  background: black;
  z-index: 999;
}
</style>