<template>
  <div class="sucais">
    <div class="header">
      <div class="back">
        <img src="../assets/left2.png" alt="" class="back" />
      </div>
    </div>
    <div class="sucai">
      <div class="top">
        <h3>素材管理</h3>
        <div class="search">
          <span class="iconfont icon-31sousuo"></span>
          <input
            type="text"
            v-model="sucai"
            placeholder="请输入素材名称/标签"
          />
        </div>
      </div>
      <div class="upload">
        <upload ref="input"></upload>
      </div>
      <div class="btm">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="sizes, prev, pager, next"
          :total="100"
        >
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import upload from "../components/upload.vue";
import { sucai } from "../api/sucai";
export default {
  components: {
    upload,
  },
  data() {
    return {
      a: "",
      sucai: "",
      fileList: [],
      currentPage: 1,
      pageSize: 10,
    };
  },
  methods: {},
  watch: {
    sucai(e) {
      this.sucai = e;
      let arr2 = this.$refs.input.fileList;
      let newvalue = arr2.filter((item) => item.name.indexOf(this.sucai) != -1);
      if (this.sucai != "") {
        this.$refs.input.fileList = newvalue;
      } else {
        this.$refs.input.fileList = JSON.parse(sessionStorage.getItem("file"));
        console.log(this.$refs.input.fileList);
      }
    },
  },
  methods: {
    handleCurrentChange(e) {
      this.currentPage = e;
      console.log(e);

      sucai({ pageNum: this.currentPage, pageSize: "10" }).then((res) => {
        console.log(res);
        let arr = res.rows;
        arr.forEach((item) => {
          this.$set(item, "url", require("@/assets/sucais.png"));
        });
        this.$refs.input.fileList = arr;
      });
    },
    handleSizeChange(val) {
      console.log(val);
      this.pageSize = val;
      sucai({ pageNum: this.currentPage, pageSize: this.pageSize }).then(
        (res) => {
          console.log(res);
          let arr = res.rows;
          arr.forEach((item) => {
            this.$set(item, "url", require("@/assets/sucais.png"));
          });
          this.$refs.input.fileList = arr;
        }
      );
    },
  },
  mounted() {
    this.fileList = JSON.parse(sessionStorage.getItem("file"));
    // console.log(this.fileList);
    // sucai().then((res)=>{
    //     console.log(res);
    //     this.fileList = res.rows
    // })
  },
};
</script>

<style scoped>
