  box-shadow: 0px 4px 16px 1px rgba(25, 25, 33, 0.24);
  border-radius: 6px 6px 6px 6px;
  position: relative;
  top: 20px;
}
.header {
  display: flex;
  align-items: center;
  gap: 12px;
}
.header .back {
  width: 56px;
  height: 45px;
  background: #171c1f;
  border-radius: 6px 6px 6px 6px;
  position: relative;
}
.header .back img {
  width: 27px;
  height: 27px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}
.header .page {
  width: 131px;
  height: 45px;
  background: #171c1f;
  border-radius: 6px 6px 6px 6px;
  line-height: 45px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
.header .page span {
  font-size: 16px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #f4f0e9;
  text-align: center;
}
.header .page img {
  width: 16px;
  height: 16px;
  /* position: absolute;
  top: 14px;
  right: 14px; */
}
.sucai .top {
  width: 100%;
  height: 150px;
  color: #f4f0e9;
  position: relative;

}
.sucai .top h3 {
  font-size: 20px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #f4f0e9;
  position: absolute;
  top: 39px;
  left: 16px;
}
.sucai .top .search {
  width: 412px;
  height: 41px;
  border-radius: 6px 6px 6px 6px;
  border: 1px solid #f4f0e9;
  position: absolute;
  top: 33px;
