  background: rgba(0, 0, 0, 0);
  color: #f4f0e9;
  font-size: 25px;
  position: absolute;
  top: 8px;
  left: 14px;
}
.sucai .top .search input {
  width: 412px;
  height: 41px;
  border-radius: 6px 6px 6px 6px;
  border: 1px solid #f4f0e9;
  background: rgba(0, 0, 0, 0);
  color: #f4f0e9;
  font-size: 25px;
  padding-left: 52px;
  outline: none;
  box-sizing: border-box;
}
.sucai .top .search input::placeholder {
  font-size: 18px;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: lighter;
  color: #f4f0e9;
